import { Timestamp } from "firebase/firestore";

export enum AppointmentStatus {
  Approved = "APPROVED",
  Canceled = "CANCELED",
  Pending = "PENDING",
  RequestToChangeDate = "REQUEST_TO_CHANGE_DATE",
}

export type AppointmentType = {
  id: string;
  companyId: string;
  status: AppointmentStatus;
  serviceName: string;
  staff: string;
  location: string;
  appointmentDate: Timestamp;
  createdAt: Timestamp;
};
